.container{
    display:grid;
    grid-template-columns: repeat(auto-fill, minmax(100px, auto));
    width:60%;
}

.item{
    height:110px;
    width:110px;
    display:flex;
    justify-content: space-between;
    flex-direction: column;
    
}
.delete{
    height:30px;
}
ul{
    list-style-type: none;
}